.mainSection{
    display: flex;
    
    justify-content: space-around;
    align-items: center;
}
.contentBox{
    width:40%;
   
    padding-left: 1em;
}
.contentBox h1{
    font-size: 2.5rem;
    color: purple;

}
.contentBox p{
    font-size: 1.2rem;
    margin:1em 0;
    font-family: 'Poppins', sans-serif;
    text-align: center;
}

.btnBox{
    width:100%;
    display: flex;
    justify-content: space-around;
    margin-top: 2em;
}
.btn{
    border:2px solid black;
    border-radius: 2em;
    background-image: linear-gradient(#fff,#fff),
    radial-gradient(circle at bottom right,purple,orange);
    background-origin:border-box;
    background-clip: content-box,border-box;
}
.readMore{
    display: inline-block;
    font-size: 1.2rem;
    padding: 0.5em 1.5em;
    text-decoration: none;
    color:purple
}
.imgContainer{
    width:50%;
}
.imgContainer img{
    width:90%;
    margin:0 auto;
    display: block;
   
}
@media (max-width:900px){
    html{
        font-size: 14px;;
    }
}

@media (max-width:768px){
    html{
        font-size: 14px;;
    }
    .mainSection{
        flex-direction: column;
        margin-top: 1em;
    }
    .contentBox {
     width: 90%;
     margin: 0 auto;
      
    
    }
    .contentBox h1{
        font-size: 2rem;
      
    
    }
    .contentBox p{
        font-size: 1.1rem;
       
    }
    .imgContainer{
        width:80%;
    }
}

@media (max-width:400px){
    .imgContainer{
        width:60%;
    }  
}