:root{
    --mainColor: #9376E0;
}
header{
    background-color: var(--mainColor);
}

.container{
    width:95%;
    margin : 0 auto;
    max-width:1200px;
    
}

.container-flex{
   display: flex;
   justify-content: space-between;
   align-items: center;

}

.logo{
    width:90px;
    padding:.2em 0;
}
.list{
    display: flex;
}

.listItem{
    margin-left: 2em;
    color: white;
    text-decoration: none;
    font-size: 1.5 em;
    font-family: 'Poppins', sans-serif;
}
.activeItem{
    color:orange;
    border-bottom: 0.1em double white;
}
.icon{
    color:white;
    font-size: 1.5rem !important;
    margin-right: 1em;
    margin-left: 1.5em;
}

@media  (max-width:1100px){
    .icon{
         margin-left: 1em;
    }
}

@media (max-width:768px){
    html{
        font-size:13px;
    }
    .container-flex{
        flex-direction:column;
     
     }
     .logo{
        width:60px;
     }
     .icon{
         margin:1em 1.5em;
     }
}

@media (max-width:568px){
    html{
        font-size:10px;
    }
    .list{
        flex-direction:column;
    }
    .listItem{
        margin-top: .2em;
    }
    .logo{
        width: 50px;
    }
}   