.contact{
    max-width: 990px;
    margin:2em auto;

}
input{
    width:100%;
    padding: 0.7em;
    margin-bottom: 1em;
    border:0.3em double var(--mainColor);
    outline: none;
    font-size: 1.1rem;
}
label{
    font-size: 1.1rem;
    font-weight: bold;
}

@media (max-width:768px){
    .contact{
        width: 70%;
        margin:1em auto;
    }
    input{
        width:100%;
        padding: 0.3em;
         border:0.1em solid var(--mainColor);
      
    }

}
@media  (max-width:1000px){
    .contact{
        width: 90%;
        
    }
    

}